.country-filters {
  margin-top: -10px;
  margin-bottom: 21px;
  padding-top: 31px;
  padding-bottom: 15px;
  background-color: $country-filter-color;
  color: $special-blue;
  border-radius: 0 0 15px 15px;

  @media (min-width: $tablet-width) {
    margin-top: -45px;
    margin-bottom: 43px;
    padding-top: 108px;
    padding-bottom: 60px;
    border-radius: 0 0 60px 60px;
  }
}

.country-filters__container {
  display: flex;
  align-items: center;

  .country-filters__button {
    display: none;
    transition: $basic-animation;
  }
}

.country-filters__heading {
  position: relative;
  margin-bottom: 1px;
  font-size: inherit;
  line-height: 18px;
  font-weight: 700;
  text-transform: uppercase;
  color: inherit;
  flex-grow: 1;

  &::after {
    position: absolute;
    width: 16px;
    height: 10px;
    top: calc(50% - 5px);
    left: 0;
    background: url("../img/icon_filter.svg");
    content: "";
  }

  @media (min-width: $tablet-width) {
    display: none;
  }
}

.country-filters__dropdown {
  position: static;
  display: block;
}

.country-filters__button {
  position: relative;
  border: none;
  width: 12px;
  height: 12px;
  font-family: $main-font;
  font-size: 20px;
  line-height: 20px;
  font-weight: 700;
  color: $special-blue;
  text-transform: uppercase;
  background-color: transparent;

  &::after {
    position: absolute;
    width: 3px;
    height: 3px;
    left: 5px;
    background-color: $special-blue;
    border-radius: 50%;
    box-shadow: 7px 0 0 0 $special-blue, -7px 0 0 0 $special-blue;
    content: "";
    transition: $basic-animation;
  }

  @media (min-width: $tablet-width) {
    &::after {
      width: 6px;
      height: 6px;
      box-shadow: 12px 0 0 0 $special-blue, -12px 0 0 0 $special-blue;
    }
  }
}

.country-filters__info {
  display: none;
}

.country-filters--open {
  position: relative;

  .country-filters__container {
    align-items: flex-start;
  }

  .country-filters__button {
    display: block;
    padding: 0;
    width: 16px;
    height: 16px;

    &::after {
      top: 50%;
      left: 0;
      width: 18px;
      height: 2px;
      transform: rotate(45deg);
      border-radius: 0;
      box-shadow: none;
    }

    &::before {
      position: absolute;
      top: 50%;
      left: 0;
      width: 18px;
      height: 2px;
      background-color: $basic-blue-dark;
      transform: rotate(-45deg);
      content: "";
      transition: $basic-animation;
    }

    @media (min-width: $tablet-width) {
      display: flex;
      margin-left: auto;
      margin-right: 33px;
      align-items: center;
      width: 185px;

      &::after {
        top: 6px;
        left: 91%;
      }
    }
  }

  .country-filters__heading {
    max-width: 250px;
  }

  .country-filters__dropdown {
    position: absolute;
    top: 50px;
    left: 0;
    width: 100%;
    padding: 30px 25px 28px;
    z-index: 3;
    background-color: $country-filter-color;
    border-radius: 0 0 20px 20px;
  }

  @media (min-width: $tablet-width) {
    position: relative;

    .country-filters__info--closed {
      display: none;
    }

    .country-filters__info--opened {
      display: block;
    }

    .continents__list {
      display: flex;
    }

    .continents__item:not(:last-child) {
      margin-right: 28px;
    }

    .country-filters__dropdown {
      position: absolute;
      top: 200px;
      left: 32px;
      display: block;
      font-size: 20px;
      line-height: 20px;
    }

    .dropdown-list__button {
      display: block;
    }

    .country-filters__button {
      display: flex;
      margin-left: auto;
      margin-right: 32px;
      align-items: center;
      width: 185px;

      &::after {
        left: 100%;
      }

      &::before {
        left: 100%;
      }
    }
  }
}

.country-filters--close {
  .country-filters__container {
    align-items: flex-start;
  }

  .country-filters__button {
    display: block;
    padding: 0;
    width: 16px;
    height: 16px;
  }

  .country-filters__dropdown {
    display: none;
  }

  @media (min-width: $tablet-width) {
    position: relative;

    .country-filters__info--closed {
      display: block;
    }

    .country-filters__button {
      display: flex;
      margin-left: auto;
      margin-right: 33px;
      align-items: center;
      width: 185px;

      &::after {
        top: 6px;
        left: 91%;
      }
    }

    .continents__list {
      display: flex;
    }

    .continents__item:not(:last-child) {
      margin-right: 28px;
    }

    .country-filters__dropdown {
      position: absolute;
      top: 106px;
      left: 32px;
      display: block;
      font-size: 20px;
      line-height: 20px;
    }

    .dropdown-list__button {
      display: none;
    }
  }
}
